export default {

	data() {
		return {
			team:'',
			teamAchievement:'',
			ratio:'',
			level:'',
			invite:''
		};
	},
	onLoad() {
		if (!uni.getStorageSync("token") && !uni.getStorageSync("SecretKey")) {
			this.$base1._isLogin()
		}
		this.initData()
	},
	onReady() {
	},
	onPullDownRefresh() {
		this.initData()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		initData() {
			//我的团队上面蓝色框框的数据
			uni.request({
				url: this.baseUrl + "/member-invite",
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.team = res.data.data.Team
						this.teamAchievement = res.data.data.TeamAchievement
						this.ratio = res.data.data.Ratio
						this.level = res.data.data.Level
						this.invite = res.data.data.Invite
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		jumpToCurrencyDetail() {
			uni.navigateTo({
				url: './currency-detail?coreId=' + this.coreId
			})
		},
		scan() {
			//扫一扫		
			// 允许通过相机扫码和相册
			uni.scanCode({
				onlyFromCamera: false,
				success: function(res) {
					console.log('条码类型：' + res.scanType);
					console.log('条码内容：' + res.result);
				}
			});
		},
		noSee() {
			this.show = !this.show
			if (this.show == false) {
				this.password = true
			} else {
				this.password = false
			}
		},
		
	}
}
